import React from "react";
import { Splide, SplideSlide, SplideTrack } from "@splidejs/react-splide";
import { StaticImage } from "gatsby-plugin-image";
import { withTranslation } from "react-i18next";

function MTen({ t }) {
  return (
    <div className="scroll-snap" id="football">
      <div className="grid" style={{ marginBottom: 20 }}>
        <div className="grid-item img" style={{ alignSelf: "flex-start" }}>
          <StaticImage
            src="../images/football.png"
            alt="Football"
            width={500}
            height={500}
            placeholder="blurred"
          />
        </div>
        <div className="grid-item content">
          <h1>M10</h1>
          <video
            className="video"
            playsInline
            controls
            poster="/TrailerM10.jpg"
            aria-hidden="true"
            style={{ marginBottom: 20 }}
          >
            <source src="/TrailerM10.mp4" type="video/mp4" />
            <track kind="captions" />
          </video>
          <p>{t("m10_description")}</p>
        </div>
      </div>

      <div className="slide-container">
        <Splide
          options={{
            perPage: 1,
            perMove: 1,
            gap: "1rem",
            height: 536,
            width: "100%",
            pagination: false,
            wheel: false,
            arrows: true,
            type: "loop",
            breakpoints: {
              1300: {
                height: 500,
              },
              1024: {
                height: 400,
              },
              768: {
                height: undefined,
                autoHeight: true,
              },
            },
          }}
          className="slider"
          hasTrack={false}
        >
          <SplideTrack>
            {characters.map((character, idx) => (
              <SplideSlide key={idx}>
                <div className="slide">
                  <img src={character.image} alt={character.name} />
                  <p className="caption">{character.name}</p>
                </div>
              </SplideSlide>
            ))}
          </SplideTrack>
          <div className="splide__arrows"></div>
        </Splide>
      </div>
    </div>
  );
}
const characters = [
  {
    name: "",
    image: "/characters/football/futbol_3.png",
  },
  {
    name: "",
    image: "/characters/football/futbol_4.png",
  },
  {
    name: "",
    image: "/characters/football/futbol_5.png",
  },
  {
    name: "",
    image: "/characters/football/futbol_6.png",
  },
  {
    name: "",
    image: "/characters/football/futbol_7.png",
  },
];

export default withTranslation()(MTen);
