import React from "react";
import { Splide, SplideSlide, SplideTrack } from "@splidejs/react-splide";
import { StaticImage } from "gatsby-plugin-image";
import { withTranslation } from "react-i18next";

function YorudanDistrikt({ t }) {
  return (
    <div className="scroll-snap" id="basket">
      <div className="grid" style={{ marginBottom: 20 }}>
        <div className="grid-item img" style={{ alignSelf: "flex-start" }}>
          <StaticImage
            src="../images/basketball.png"
            alt="Basket"
            width={500}
            height={500}
            placeholder="blurred"
          />
        </div>
        <div className="grid-item content">
          <h1>Yorudan Distrikt</h1>
          <video
            className="video"
            playsInline
            controls
            style={{ marginBottom: 20 }}
          >
            <source src="/basket.mp4" type="video/mp4" />
            <track kind="captions" />
          </video>
          <p>{t("yorudan_description")}</p>
        </div>
      </div>
      <div className="slide-container">
        <Splide
          options={{
            perPage: 1,
            perMove: 1,
            gap: "1rem",
            height: 600,
            width: "100%",
            pagination: false,
            wheel: false,
            arrows: true,
            type: "loop",
            breakpoints: {
              1300: {
                height: 500,
              },
              1024: {
                height: 400,
              },
              540: {
                height: 360,
              },
              400: {
                height: 300,
              },
            },
          }}
          className="slider"
          hasTrack={false}
        >
          <SplideTrack>
            {characters.map((character, idx) => (
              <SplideSlide key={idx}>
                <div className="slide">
                  <img src={character.image} alt={character.name} />
                  <p className="caption">{character.name}</p>
                </div>
              </SplideSlide>
            ))}
          </SplideTrack>
          <div className="splide__arrows"></div>
        </Splide>
      </div>
    </div>
  );
}
const characters = [
  { name: "", image: "/characters/basket/basketball_1.png" },
  { name: "", image: "/characters/basket/basketball_2.png" },
  { name: "", image: "/characters/basket/basketball_4.png" },
  { name: "", image: "/characters/basket/basketball_5.png" },
  { name: "", image: "/characters/basket/basketball_6.png" },
  { name: "", image: "/characters/basket/basketball_7.png" },
  { name: "", image: "/characters/basket/basketball_8.png" },
  { name: "", image: "/characters/basket/basketball_9.png" },
  { name: "", image: "/characters/basket/basketball_10.png" },
  { name: "", image: "/characters/basket/basketball_11.png" },
  { name: "", image: "/characters/basket/basketball_12.png" },
];

export default withTranslation()(YorudanDistrikt);
