import React from "react";
import { withTranslation } from "react-i18next";

function LanguageSwitcher({ i18n }) {
  function handleChangeLanguage(lng) {
    console.log("LanguageSwitcher.handleChangeLanguage", lng);
    i18n.changeLanguage(lng);
  }

  function renderLanguageChoice({ code, label, title }) {
    const buttonClass = i18n.language !== code ? "lang-btn" : "lang-btn active";

    return (
      <button
        key={code}
        className={buttonClass}
        onClick={() => handleChangeLanguage(code)}
        title={title}
      >
        {label}
      </button>
    );
  }

  const languages = [
    { code: "en", label: "EN", title: "English" },
    { code: "es", label: "ES", title: "Español" },
  ];

  return (
    <div className="lang-switch">
      {languages.map((language) => renderLanguageChoice(language))}
    </div>
  );
}

export default withTranslation()(LanguageSwitcher);
