import React, { useState } from "react";
import { withTranslation } from "react-i18next";

function FAQs({ t }) {
  const [active, setActive] = useState(null);
  function handleClick(index) {
    if (active === index) {
      setActive(null);
    } else {
      setActive(index);
    }
  }

  const faqs = [
    { question: t("faq_question_1"), answer: t("faq_answer_1") },
    { question: t("faq_question_2"), answer: t("faq_answer_2") },
    { question: t("faq_question_3"), answer: t("faq_answer_3") },
    { question: t("faq_question_4"), answer: t("faq_answer_4") },
    { question: t("faq_question_5"), answer: t("faq_answer_5") },
    { question: t("faq_question_6"), answer: t("faq_answer_6") },
    { question: t("faq_question_7"), answer: t("faq_answer_7") },
  ];
  return (
    <div id="faq">
      <div className="f-grid">
        <div className="accordion-wrapper">
          <h1 title="Frequently Asked Questions">F.A.Q.</h1>
          {faqs.map((faq, index) => (
            <div
              key={index}
              className={`panel ${active === index ? "active" : ""}`}
            >
              <button className="accordion" onClick={() => handleClick(index)}>
                <span>{faq.question}</span>
                <div className="arrow-down" />
              </button>
              <p dangerouslySetInnerHTML={{ __html: faq.answer }} />
            </div>
          ))}
          <p className="copy">&copy; {new Date().getFullYear()} DISTRIKT</p>
        </div>
        <p className="copy">&copy; {new Date().getFullYear()} DISTRIKT</p>
      </div>
    </div>
  );
}

export default withTranslation()(FAQs);
