import React from "react";
import { StaticImage } from "gatsby-plugin-image";
import { withTranslation } from "react-i18next";

function About({ t }) {
  return (
    <div className="scroll-snap about-content" id="about">
      <div className="logo">
        <StaticImage
          src="../images/about-logo.png"
          alt="Hero"
          placeholder="blurred"
        />
      </div>
      <p dangerouslySetInnerHTML={{ __html: t("about_description") }}></p>
    </div>
  );
}

export default withTranslation()(About);
