import SweetScroll from "sweet-scroll";
import React, { useEffect, useRef, useState } from "react";
import { StaticImage } from "gatsby-plugin-image";
import LanguageSwitcher from "./LanguageSwitcher";
import { withTranslation } from "react-i18next";

const renderSocials = (className = "socials") => (
  <ul className={className}>
    <li>
      <a href="https://www.youtube.com/@DistriktCNFT" title="YouTube">
        <StaticImage
          src="../images/youtube.png"
          alt="YouTube"
          width={24}
          placeholder="blurred"
        />
      </a>
    </li>
    <li>
      <a href="https://www.instagram.com/distriktcnft" title="Instagram">
        <StaticImage
          src="../images/instagram.png"
          alt="Instagram"
          width={20}
          placeholder="blurred"
        />
      </a>
    </li>
    <li>
      <a href="https://twitter.com/DISTRIKTCNFT" title="Twitter">
        <StaticImage
          src="../images/twitter.png"
          alt="Twitter"
          width={20}
          placeholder="blurred"
        />
      </a>
    </li>
    <li>
      <a href="https://discord.gg/EApn2qm8" title="Discord">
        <StaticImage
          src="../images/discord.png"
          alt="Discord"
          width={20}
          placeholder="blurred"
        />
      </a>
    </li>
  </ul>
);

const renderSubmenu = () => (
  <ul>
    <li>
      <a
        href="https://www.jpg.store/collection/distriktpass"
        target="_blank"
        rel="noreferrer"
      >
        Distrikt Pass
      </a>
    </li>
    <li>
      <a
        href="https://www.jpg.store/collection/distriktairdrops?tab=items"
        target="_blank"
        rel="noreferrer"
      >
        Airdrops
      </a>
    </li>
    <li>
      <a href="/#">Main Collection</a>
    </li>
  </ul>
);

function Navbar({ t }) {
  const [scrollTriggered, setScrollTriggered] = useState(false);
  const [menuOpen, setMenuOpen] = useState(false);
  const [nestedMenuOpen, setNestedMenuOpen] = useState(false);
  const sweetScroll = useRef(null);

  useEffect(() => {
    const handleScroll = () => {
      if (window.scrollY > 50) {
        setScrollTriggered(true);
      } else {
        setScrollTriggered(false);
      }
    };
    window.addEventListener("scroll", handleScroll);
    sweetScroll.current = new SweetScroll({
      easing: "linear",
    });
    return () => {
      window.removeEventListener("scroll", handleScroll);
    };
  }, []);

  function handleClick(goTo) {
    sweetScroll.current.to(goTo);
    setMenuOpen(false);
  }

  function toggleMenu() {
    setMenuOpen(!menuOpen);
  }

  function toggleNestedMenu() {
    setNestedMenuOpen(!nestedMenuOpen);
  }

  const renderMenuBtn = (name, mobile = false) => {
    const className = mobile ? "" : "n-" + name;
    return (
      <li className={className}>
        <div
          role="button"
          tabIndex={0}
          onClick={() => handleClick("#" + name)}
          onKeyDown={() => handleClick("#" + name)}
        >
          {t(name)}
        </div>
      </li>
    );
  };

  return (
    <nav className="nav-bar">
      <div className="nav-bg" style={{ opacity: scrollTriggered ? 1 : 0 }} />
      <div
        className="brand"
        role="button"
        tabIndex={0}
        onClick={() => handleClick("#index")}
        onKeyDown={() => handleClick("#index")}
      >
        <StaticImage
          src="../images/distrikt-brand.png"
          alt="Distrikt"
          width={130}
          placeholder="blurred"
        />
      </div>
      <ul className="pages">
        {renderMenuBtn("about")}
        <li className="n-distrikt-pass n-airdrops n-main-collection">
          <div className="dropdown">
            <div className="arrow-down" />
            <span title="Non Fungible Tokens">NFTs</span>
            <div className="dropdown-wrapper">{renderSubmenu()}</div>
          </div>
        </li>
        {renderMenuBtn("world")}
        {renderMenuBtn("crew")}
        {renderMenuBtn("faq")}
      </ul>
      {renderSocials("socials desktop")}
      <LanguageSwitcher />
      {menuOpen && (
        <div
          className="overlay"
          role="button"
          tabIndex={0}
          aria-label="Menu"
          onClick={toggleMenu}
          onKeyDown={toggleMenu}
        />
      )}
      <div id="menuToggle">
        <div
          className={`ham ${menuOpen ? "open" : ""}`}
          role="button"
          tabIndex={0}
          onClick={toggleMenu}
          onKeyDown={toggleMenu}
        >
          <span />
          <span />
          <span />
        </div>
        <ul id="mobileMenu" className={menuOpen ? "open" : ""}>
          {renderMenuBtn("about", true)}
          <li>
            <div
              className="nft-menu"
              role="button"
              tabIndex={0}
              onClick={toggleNestedMenu}
              onKeyDown={toggleNestedMenu}
            >
              <span title="Non Fungible Tokens">NFTs</span>
              <div className={`arrow-down ${nestedMenuOpen ? "open" : ""}`} />
            </div>
          </li>
          <li className={`nested-menu ${nestedMenuOpen ? "open" : ""}`}>
            {renderSubmenu()}
          </li>
          {renderMenuBtn("world", true)}
          {renderMenuBtn("crew", true)}
          {renderMenuBtn("faq", true)}
          <li>
            <hr />
          </li>
          <li>{renderSocials()}</li>
        </ul>
      </div>
    </nav>
  );
}

export default withTranslation()(Navbar);
