import SweetScroll from "sweet-scroll";
import React, { useEffect, useRef } from "react";

// import wheel from "../images/wheel.svg";
import { StaticImage } from "gatsby-plugin-image";

const imgs = {
  graffiti: {
    png: "/imgs/graffiti.png",
    gif: "/imgs/graffiti.gif",
    link: "#graffiti",
  },
  basket: {
    png: "/imgs/basketball.png",
    gif: "/imgs/basketball.gif",
    link: "#basket",
  },
  football: {
    png: "/imgs/football.png",
    gif: "/imgs/football.gif",
    link: "#football",
  },
  // quad: {
  //   png: "/imgs/quad.png",
  //   gif: "/imgs/quad.gif",
  //   link: "#skate",
  // },
  skate: {
    png: "/imgs/skate.png",
    gif: "/imgs/skate.gif",
    link: "#skate",
  },
};

export default function World() {
  const world = useRef(null);
  const wheel = useRef(null);
  const sweetScroll = useRef(null);

  useEffect(() => {
    sweetScroll.current = new SweetScroll({
      easing: "linear",
    });
  }, []);

  function handleClick(goTo) {
    sweetScroll.current.to(goTo);
  }

  function handleMouseEnter(e, key) {
    e.target.src = imgs[key].gif;
    world.current.classList.add(key);
    setTimeout(() => {
      world.current.classList.remove(key);
    }, 2500);
  }

  function handleMouseLeave(e, key) {
    e.target.src = imgs[key].png;
    world.current.classList.remove(key);
  }

  function handleSpin(e) {
    e.stopPropagation();
    wheel.current.classList.add("spin");
    setTimeout(() => {
      wheel.current.classList.remove("spin");
    }, 4050);
  }

  return (
    <div className="scroll-snap world" id="world" ref={world}>
      <div className="world-contents">
        <div className="circle-outer" ref={wheel}>
          <div className="items">
            {Object.keys(imgs).map((key, idx) => (
              <div className={"item-" + (idx + 1)} key={key}>
                <div
                  role="button"
                  tabIndex={0}
                  to={imgs[key].link}
                  onClick={() => handleClick(imgs[key].link)}
                  onKeyDown={() => handleClick(imgs[key].link)}
                >
                  <img
                    src={imgs[key].png}
                    alt={key}
                    role="presentation"
                    onMouseEnter={(e) => handleMouseEnter(e, key)}
                    onMouseLeave={(e) => handleMouseLeave(e, key)}
                  />
                </div>
              </div>
            ))}
          </div>
          <div
            className="circle-inner"
            role="button"
            onClick={handleSpin}
            onKeyDown={handleSpin}
            tabIndex={0}
          >
            <StaticImage
              src="../images/distrikt-brand.png"
              alt="Circle"
              width={180}
              style={{ imageRendering: "crisp-edges", filter: "invert(80%)" }}
            />
          </div>
          <div className="world-bg" />
        </div>
      </div>
    </div>
  );
}
